body {
    box-sizing: border-box;
    font-family: "Dancing Script", cursive;
    font-family: "Montserrat", sans-serif;
    font-family: "Roboto", sans-serif;
    width: 100%;
}

h1,
h2,
h3,
h4 {
    margin: 0;
}
p {
    margin: 0;
}
a {
    text-decoration: none;
}
li {
    list-style: none;
}
